<template>
  <div class="precontainer">
    <div class="container">
      <form @submit.prevent="loginUser" class="login form"><span class="login-icon">Wolimby</span>
        <h2 class="login-title">Jelentkezz be</h2>
        <div class="login-row">
          <input class="login-input" v-model="username" id="lg-us" type="text" required>
          <label class="login-label" for="lg-us">Felhasználónév</label>
        </div>
        <div class="login-row">
          <input class="login-input" v-model="password" id="lg-ps" type="password" required>
          <label class="login-label" for="lg-ps">Jelszó</label>
        </div>
        <div class="login-check">
          <input class="login-checkbox" v-model="checkbox" id="lg-ch" type="checkbox">
          <label for="lg-ch">Maradj bejelentkezve</label>
        </div>
        <div class="login-row">
          <button class="login-button" type="submit">Bejelentkezés</button>
        </div>
        <div class="login-row"><a class="login-link">Elfelejtetted a jelszavad?</a></div>
      </form>
      <form @submit.prevent="registerUser" class="signup is-form-open form" id="signup">
        <svg class="svg-icon signup-trigger signup-trigger--fixed" @click="signUpClick" viewBox="0 0 20 20">
          <path d="M14.613,10c0,0.23-0.188,0.419-0.419,0.419H10.42v3.774c0,0.23-0.189,0.42-0.42,0.42s-0.419-0.189-0.419-0.42v-3.774H5.806c-0.23,0-0.419-0.189-0.419-0.419s0.189-0.419,0.419-0.419h3.775V5.806c0-0.23,0.189-0.419,0.419-0.419s0.42,0.189,0.42,0.419v3.775h3.774C14.425,9.581,14.613,9.77,14.613,10 M17.969,10c0,4.401-3.567,7.969-7.969,7.969c-4.402,0-7.969-3.567-7.969-7.969c0-4.402,3.567-7.969,7.969-7.969C14.401,2.031,17.969,5.598,17.969,10 M17.13,10c0-3.932-3.198-7.13-7.13-7.13S2.87,6.068,2.87,10c0,3.933,3.198,7.13,7.13,7.13S17.13,13.933,17.13,10"></path>
        </svg>
        <div class="signup-wrapper is-wrapper-open" id="signup-wrapper">
          <div class="signup-row signup-row--flex"><span class="signup-icon">Wolimby</span>
            <svg class="svg-icon signup-trigger" @click="signUpClick" viewbox="0 0 20 20">
              <path d="M10.185,1.417c-4.741,0-8.583,3.842-8.583,8.583c0,4.74,3.842,8.582,8.583,8.582S18.768,14.74,18.768,10C18.768,5.259,14.926,1.417,10.185,1.417 M10.185,17.68c-4.235,0-7.679-3.445-7.679-7.68c0-4.235,3.444-7.679,7.679-7.679S17.864,5.765,17.864,10C17.864,14.234,14.42,17.68,10.185,17.68 M10.824,10l2.842-2.844c0.178-0.176,0.178-0.46,0-0.637c-0.177-0.178-0.461-0.178-0.637,0l-2.844,2.841L7.341,6.52c-0.176-0.178-0.46-0.178-0.637,0c-0.178,0.176-0.178,0.461,0,0.637L9.546,10l-2.841,2.844c-0.178,0.176-0.178,0.461,0,0.637c0.178,0.178,0.459,0.178,0.637,0l2.844-2.841l2.844,2.841c0.178,0.178,0.459,0.178,0.637,0c0.178-0.176,0.178-0.461,0-0.637L10.824,10z"></path>
            </svg>
          </div>
          <h2 class="signup-title">Regisztrálj be</h2>
          <div class="signup-row">
            <input class="signup-input" v-model="username" id="su-us" type="text" required>
            <label class="signup-label" for="su-us">Felhasználónév</label>
          </div>
          <div class="signup-row">
            <input class="signup-input" v-model="email" id="su-em" type="text" required>
            <label class="signup-label" for="su-em">E-mail cím</label>
          </div>
          <div class="signup-row">
            <input class="signup-input" v-model="password" id="su-ps" type="password" required>
            <label class="signup-label" for="su-ps">Jelszó</label>
          </div>
          <div class="signup-check">
            <input class="signup-checkbox" id="su-ch" type="checkbox" required>
            <label for="su-ch">Elfogadom a Felhasználási feltételeket</label>
          </div>
          <div class="signup-row">
            <button class="signup-button" type="submit">Regisztráció</button>
          </div>
          <div class="signup-row"><a class="signup-link">Elfelejtetted a jelszavad?</a></div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import '@/assets/scss/auth.scss';
export default {
  name: 'Auth',

  data() {
    return {
      username: '',
      email: '',
      password: '',
      checkbox: false,
    }
  },

  computed: {
    ...mapGetters(['userError']),
  },

  methods: {
    ...mapActions(['login', 'register', 'addNotification']),

    signUpClick() {
      const signupTrigger = document.querySelectorAll('.signup-trigger');
      const signupElement = document.querySelector('#signup');
      const signupWrapperElement = document.querySelector("#signup-wrapper");
      
      signupTrigger[0].classList.toggle('is-trigger-hidden');
      signupElement.classList.toggle('is-form-open');
      signupWrapperElement.classList.toggle('is-wrapper-open');
    },

    loginUser() {
      const user = {
        username: this.username,
        password: this.password,
      }

      this.login([user, this.checkbox]).then(res => {
        if(res.data.success) {
          this.$router.push('/access');

          this.addNotification({
            message: 'Sikeres bejelentkezés.',
            type: 'success'
          });
        }
      }).catch(() => {
        const notification = {
            message: this.userError || 'Ismeretlen hiba.',
            type: 'error'
          }

        this.addNotification(notification);
      });
    },

    registerUser() {
      const user = {
        username: this.username,
        email: this.email,
        password: this.password,
      }

      this.register(user).then(res => {
        if(res.data.success) {

          const notification = {
            message: 'Sikeres regisztráció, most kérlek jelentkezz be.',
            type: 'success'
          }

          this.addNotification(notification);
        }
      }).catch(() => {
        const notification = {
          message: this.userError || 'Ismeretlen hiba.',
          type: 'error'
        }

        this.addNotification(notification);
      });
    }
  }
}
</script>